@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@700&display=swap);
header {
  width: 100%; /* Asegura que el header ocupe todo el ancho de la página */
  background-color: #001f3f; /* Color de fondo azul oscuro */
  color: white;
  text-align: center; /* Centra el contenido */
  padding: 20px 0;
}

.header-title h1 {
  font-size: 3rem; /* Tamaño grande para el título */
  margin: 0;
  font-family: Arial, sans-serif; /* O cualquier fuente que prefieras */
  letter-spacing: 3px; /* Espaciado entre letras */
  color: #fff; /* Color blanco para el texto */
}

.header-nav {
  background-color: #001f3f; /* Fondo azul oscuro para la barra de navegación */
  padding: 10px 0;
}

.header-nav a {
  color: white; /* Letras blancas */
  text-decoration: none;
  margin: 0 15px;
  font-weight: bold;
  transition: color 0.3s, transform 0.3s; /* Efecto suave al pasar el ratón */
}

.header-nav a:hover {
  color: yellow; /* Cambia el color a amarillo al pasar el ratón */
  transform: scale(1.1); /* Aumenta ligeramente el tamaño del texto al pasar el ratón */
}


  
footer {
  background-color: #001f3f; /* Azul oscuro */
  color: white; /* Letras blancas */
  text-align: center;
  padding: 20px;
  position: relative;
  width: 100%;
  bottom: 0;
}

.social-icons {
  margin-top: 10px;
}

.social-icons a {
  color: white; /* Color blanco por defecto */
  margin: 0 10px;
  font-size: 24px;
  transition: transform 0.3s, color 0.3s; /* Transiciones suaves */
}

.social-icons a:hover {
  transform: scale(1.2); /* Agranda el ícono */
  color: yellow; /* Cambia el color a amarillo */
}



