/* Reset de márgenes y padding global */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
  }
  
  /* Estilo para la página de contacto */
  .contact-container {
    width: 100%;
    text-align: center;
    padding: 20px;
  }
  
  /* Estilo del header */
  .contact-header {
    background-color: #001f3f; /* Azul oscuro */
    color: white;
    padding: 50px 20px;
  }
  
  .contact-header h1 {
    font-size: 3rem;
    margin-bottom: 10px;
  }
  
  .contact-header p {
    font-size: 1.2rem;
    margin-bottom: 30px;
  }
  
  /* Estilos para el formulario de contacto */
  .contact-form-section {
    padding: 40px 20px;
    background-color: #ffffff;
  }
  
  .contact-form {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-group label {
    display: block;
    font-size: 1rem;
    font-weight: bold;
    color: #333;
    margin-bottom: 5px;
  }
  
  .form-group input,
  .form-group textarea {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ddd;
    border-radius: 8px;
    margin-top: 5px;
  }
  
  .form-group input:focus,
  .form-group textarea:focus {
    border-color: #0066cc;
    outline: none;
  }
  
  textarea {
    resize: vertical;
    height: 150px;
  }
  
  /* Botón de enviar */
  .submit-btn {
    background-color: #0066cc;
    color: white;
    font-size: 1.2rem;
    padding: 10px 20px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .submit-btn:hover {
    background-color: #004d99;
  }
  
  /* Mensaje de confirmación */
  .submission-message {
    margin-top: 20px;
    font-size: 1.2rem;
    color: green;
  }
  
  
/* Reset de márgenes y padding global */
* {
  margin: 0;
